import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getPathNumber, getCodedEmail } from 'helper/configConst';
import { profileAuth } from 'actions/index';
import { withRouter } from 'react-router-dom';
import UsetAuth from 'components/helpers/UsetAuth';

class SendReset extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedOption: 'phone'
    };
    this.onSubmit = this.onSubmit.bind(this)
    this.handleOptionChange = this.handleOptionChange.bind(this)
  }
  componentWillMount() {
    this.props.resetClear()
  }
  handleOptionChange(e) {
    this.setState({selectedOption: e.target.value})
  }

  onSubmit (e) {
    console.log("onSubmit",e)
    e.preventDefault();
    this.props.resetIn(this.state.selectedOption)
    this.props.history.push('/reset/confirm_pin')
  }
  
  render () {
    return (
      <div>
        <h1>ÐÐ°Ðº Ð²Ñ ÑÐ¾ÑÐ¸ÑÐµ ÑÐ±ÑÐ¾ÑÐ¸ÑÑ Ð¿Ð°ÑÐ¾Ð»Ñ</h1>

        <UsetAuth user={this.props.user}/>

        <p className='pt-3'>ÐÑ Ð½Ð°ÑÐ»Ð¸ Ð¸Ð½ÑÐ¾ÑÐ¼Ð°ÑÐ¸Ñ, ÑÐ²ÑÐ·Ð°Ð½Ð½ÑÑ Ñ Ð²Ð°ÑÐµÐ¹ ÑÑÐµÑÐ½Ð¾Ð¹ Ð·Ð°Ð¿Ð¸ÑÑÑ.</p>

        <form onSubmit={this.onSubmit}>
          <div className='py-3'>
            <div className="form-group mb-0">
              <label>
                <input type="radio" name="reset_password" value="phone" onChange={this.handleOptionChange}  defaultChecked/>{' '}
                ÐÑÐ¿ÑÐ°Ð²Ð¸ÑÑ ÐºÐ¾Ð´ Ð½Ð° Ð¼Ð¾Ð¹ Ð½Ð¾Ð¼ÐµÑ ÑÐµÐ»ÐµÑÐ¾Ð½Ð°, Ð·Ð°ÐºÐ°Ð½ÑÐ¸Ð²Ð°ÑÑÐ¸Ð¹ÑÑ Ð½Ð° <b>{getPathNumber(this.props.user.number)}</b>
              </label>
            </div>
            <div className="form-group mb-0">
              <label >
                <input type="radio" name="reset_password" value="email" onChange={this.handleOptionChange} />{' '}
                ÐÑÐ¿ÑÐ°Ð²Ð¸ÑÑ ÑÑÑÐ»ÐºÑ Ð¿Ð¾ ÑÐ»ÐµÐºÑÑÐ¾Ð½Ð½Ð¾Ð¹ Ð¿Ð¾ÑÑÐµ <b>{getCodedEmail(this.props.user.email)}</b>
              </label>
            </div>
          </div>
          <button type='submit' className="px-3 font-weight-bold btn btn-primary btn-radius" >ÐÑÐ¾Ð´Ð¾Ð»Ð¶Ð¸ÑÑ</button>
        </form>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    user: state.user,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    resetIn: type => {
      dispatch(profileAuth.resetPassword(type))
    },
    resetClear: () => {
      dispatch(profileAuth.resetClear())
    }
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SendReset));


